'use client';
import { useEffect, useRef } from 'react';

export default function AboutPage() {
  const aboutRef = useRef<HTMLDivElement>(null);

  // Scroll automatique si on arrive avec #about
  useEffect(() => {
    if (window.location.hash === '#about' && aboutRef.current) {
      aboutRef.current.scrollIntoView({ behavior: 'smooth' });
    }
  }, []);

  return (
    <section
      ref={aboutRef}
      id="about"
      style={{
        minHeight: '100vh',
        padding: '60px 20px',
        backgroundColor: 'var(--bg-color)',
        color: 'var(--text-color)',
        transition: '0.3s',
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        textAlign: 'center',
      }}
    >
      <h2 style={{ fontSize: '2.5rem', marginBottom: '20px' }}>À propos de moi</h2>
      
      <p style={{ fontSize: '1.2rem', maxWidth: '800px', marginBottom: '30px', lineHeight: '1.6' }}>
        Je suis RAZAFITSOTRA Toslin, étudiant en 3ᵉ année de Licence en Informatique, spécialité Développement d’Applications Internet et Intranet (DAII) à l’EMIT Fianarantsoa. Passionné par le développement web et le développement de jeux vidéo, j’utilise des technologies modernes comme React et Next.js pour créer des applications performantes et intuitives. Je maîtrise Git et GitHub pour la gestion de version et le travail collaboratif. Compétent en bureautique (Word, PowerPoint, Excel, Access), je communique couramment en français et je continue de perfectionner mon anglais. Curieux et créatif, je m’inspire de mes loisirs tels que les jeux vidéo, les mangas et les anime.
      </p>

      <blockquote style={{
        fontStyle: 'italic',
        maxWidth: '700px',
        lineHeight: '1.5',
        borderLeft: '4px solid var(--btn-bg-color)',
        paddingLeft: '15px',
        color: 'var(--btn-bg-color)'
      }}>
        « L’expérience est une lanterne que l’on porte sur le dos et qui n’éclaire que le chemin parcouru. Mais chaque pas enrichit notre lumière pour les routes à venir. » – Confucius (adapté)
      </blockquote>
    </section>
  );
}


